	module.exports = {"pathContext":{"docHtml":"<h1>AMQP Gateway &#x26; Sensor</h1>\n<ol>\n<li><a href=\"#example-event-sources-definition\">Example event sources definition</a></li>\n<li><a href=\"#install-gateway\">Install gateway</a></li>\n<li><a href=\"#install-sensor\">Install sensor</a></li>\n<li><a href=\"#trigger-workflow\">Trigger Workflow</a></li>\n</ol>\n<h2>Example event sources definition</h2>\n<pre><code class=\"language-yaml\">apiVersion: v1\nkind: ConfigMap\nmetadata:\n  name: amqp-gateway-configmap\ndata:\n  foo: |-\n    url: amqp://amqp.argo-events:5672\n    exchangeName: foo\n    exchangeType: fanout\n    routingKey: fooK\n  bar: |-\n    url: amqp://amqp.argo-events:5672\n    exchangeName: bar\n    exchangeType: fanout\n    routingKey: barK\n</code></pre>\n<p>Create gateway event sources</p>\n<pre><code class=\"language-yaml\">kubectl -n argo-events create -f  https://github.com/argoproj/argo-events/blob/master/examples/gateways/amqp-gateway-configmap.yaml\n</code></pre>\n<h2>Install gateway</h2>\n<ol>\n<li>\n<p><strong>Create gateway</strong></p>\n<pre><code class=\"language-yaml\">kubectl -n argo-events create -f https://github.com/argoproj/argo-events/blob/master/examples/gateways/amqp.yaml\n</code></pre>\n</li>\n<li>\n<p><strong>Check the status of the gateway</strong></p>\n<pre><code class=\"language-yaml\">kubectl -n argo-events describe gateway amqp-gateway\n</code></pre>\n<p>Make sure the gateway is in active state and all the event sources are in running state.</p>\n</li>\n</ol>\n<h2>Install Sensor</h2>\n<pre><code class=\"language-yaml\">kubectl -n argo-events create -f https://github.com/argoproj/argo-events/blob/master/examples/sensors/amqp.yaml\n</code></pre>\n<h2>Trigger workflow</h2>\n<p>Send a message to exchange name <code>foo</code> with exchange type <code>fanout</code> and routing key as <code>fooK</code> on amqp://amqp.argo-events:5672</p>","docPath":"argo-events/gateways/core/stream/amqp/install","proj":"argo-events"}}

